.FadeOut{overflow:hidden;position:fixed;top:0;left:0;width:100%;height:100%;backdrop-filter:blur(3px);background:rgba(0,0,0,.5803921569);z-index:99999999;display:grid;place-items:center}.Modal{overflow:auto;overflow-x:hidden;background:#0c0c0c;padding:15px;border-radius:10px;max-width:50%;min-width:30%;max-height:70%}@media screen and (max-width: 767px){.Modal{max-width:100%;min-width:100%;max-height:100%}}.Modal .Title{white-space:nowrap}.Modal .Header{font-weight:bolder;font-size:20px;text-align:left;display:flex;vertical-align:middle;margin-bottom:15px}.Modal .Header .Close{display:grid;width:100%;justify-items:end}.Modal .Header .Close svg{font-size:24px;cursor:pointer}.Modal .Body{width:100%}.Modal .Footer{margin-top:15px}.Modal .Footer .ButtonGroup{display:flex;flex-direction:row}.Modal .Footer .ButtonGroup button{margin-right:15px}.Modal .Footer .ButtonGroup button:last-of-type{margin-right:0}